import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { I18nextProvider } from "react-i18next";
import i18next from "i18next";

import main_en from './translation/en/main.json';
import common_en from './translation/en/common.json';
import main_br from './translation/br/main.json';
import common_br from './translation/br/common.json';
import './index.css';
import './components/pages/Default.css';

var defLanguage = 'en';

try {
    defLanguage = localStorage.getItem('lang') || 'en';
} catch (error) {
    // ignore
}

i18next.init({
    interpolation: { escapeValue: false },  // React already does escaping
    lng: defLanguage,
    resources: {
        en: {
            main: main_en,
            common: common_en,
        },
        br: {
            main: main_br,
            common: common_br
        }
    }
});

ReactDOM.render(
    <React.StrictMode>
        <I18nextProvider i18n={i18next}>
            <App />
        </I18nextProvider>
    </React.StrictMode>,
    document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
